import React from "react";
import "./wallet.css";
import {createFragmentContainer, graphql} from "react-relay";
import {commitMutation} from "react-relay";
import environment from "../../environment";

const NewWalletMutation = graphql`
    mutation NewwalletMutation($username: String, $routingnumber: String, $accountnumber: String, 
            $zipcode: String, $creditcard: String, $cvv: String, $expmonth: String, 
            $expyear: String, $amount: Float, $type: String, $bankname: String ){
                addWallet(username: $username, routingnumber: $routingnumber, accountnumber: $accountnumber,
                zipcode: $zipcode, creditcard: $creditcard, cvv: $cvv, expmonth: $expmonth,
                expyear: $expyear, amount: $amount, type: $type, bankname: $bankname){
                    username
                }
        
    }
`
 class Newwallet extends React.Component{
    constructor(){
        super();
        this.state = {
            username: '',
            routingnumber: '',
            accountnumber: '',
            zipcode:'',
            creditcard: '',
            cvv: '',
            expmonth: '',
            expyear: '',
            amount: 0.0,
            type: '',
            bankname: ''
        }
        this.usernameChange = this.usernameChange.bind(this);
        this.routingnumberChange = this.routingnumberChange.bind(this);
        this.accountnumberChange = this.accountnumberChange.bind(this);
        this.zipcodeChange = this.zipcodeChange.bind(this);
        this.creditcardChange = this.creditcardChange.bind(this);
        this.cvvChange = this.cvvChange.bind(this);
        this.expmonthChange = this.expmonthChange.bind(this);
        this.expyearChange = this.expyearChange.bind(this);
        this.amountChange = this.amountChange.bind(this);
        this.typeChange = this.typeChange.bind(this);
        this.bankname = this.bankname.bind(this);
        this.submit = this.submit.bind(this);
    }
    usernameChange(event){
        this.setState({username: event})
    }
    routingnumberChange(event){
        this.setState({routingnumber: event.target.value})
    }
    accountnumberChange(event){
        this.setState({accountnumber: event.target.value})
    }
    zipcodeChange(event){
        this.setState({zipcode: event.target.value})
    }
    creditcardChange(event){
        this.setState({creditcard: event.target.value})
    }
    cvvChange(event){
        this.setState({cvv: event.target.value})
    }
    expmonthChange(event){
        this.setState({expmonth: event.target.value})
    }
    expyearChange(event){
        this.setState({expyear: event.target.value})
    }
    amountChange(event){
        this.setState({amount: event.target.value})
    }
    typeChange(event){
        this.setState({type: event.target.value})
    }
    bankname(event){
        this.setState({type: event.target.value})
    }
    submit(){
        
        const variables = {
            username: this.state.username,
            routingnumber: this.state.routingnumber,
            accountnumber: this.state.accountnumber,
            zipcode: this.state.zipcode,
            creditcard: this.state.creditcard,
            cvv: this.state.cvv,
            expmonth: this.state.expmonth,
            expyear: this.state.expyear,
            amount: parseFloat(this.state.amount),
            type: this.state.type,
            bankname: this.state.bankname
        }
        commitMutation(environment,{
            mutation: NewWalletMutation,
            variables,
            onCompleted: (response) => {
                const wallet = response.addWallet;
                if(wallet !== null){
                    if(wallet.username !== null){
                        window.location.reload()
                    }
                }
            }
        })
    }
    render(){
        return (
            <div className="container-fluid">
                <div className="row">
                    <div style={{"margin": "0 auto","textAlign": "center", "fontSize": "50px", "color": "#00c730"}}>     
                    </div>
                </div>
                <div className="row">
                    <div className="col-md-12" style={{"textAlign": "center"}}>
                        <h1>Add new Wallet</h1>
                        <p style={{"color":"#999","fontSize": "22px","marginTop": "50px"}}>Direct Deposit</p>
                        <div className="card " style={{"width": "18rem","margin": "0 auto"}}>
                        <div className="card-body">
                            <div className="row">
                                <div className="col-md-6">
                                    <h5><input className="form-control" value={this.state.routingnumber} onChange={this.routingnumberChange} type="text" /></h5>
                                    <h6>Routing</h6>
                                </div>
                                <div className="col-md-6">
                                    <h5><input className="form-control" value={this.state.accountnumber} onChange={this.accountnumberChange} type="text" /></h5>
                                    <h6>Account</h6>
                                </div>
                            </div>
                        </div>
                        </div>
                        <p style={{"color": "#999", "fontSize": "22px", "marginTop": "50px"}}>Card</p>
                        <div className="card col-md-6 " style={{"width": "100%","margin": "0 auto"}}>
                        <div className="card-body">
                            <div className="row">
                                <div  id="payment-form" style={{"width": "100%"}}>
                                <div className="form-row">
                                    
                                    <h5>Credit Card Number</h5>
                                    <input className="form-control" value={this.state.creditcard} onChange={this.creditcardChange} type="text" />
                                    <h5>Exp Month</h5>
                                    <input className="form-control" value={this.state.expmonth} onChange={this.expmonthChange} type="text" />
                                    <h5>Exp Year</h5>
                                    <input className="form-control" value={this.state.expyear} onChange={this.expyearChange} type="text" />
                                    <h5>CVV</h5>
                                    <input className="form-control" value={this.state.cvv} onChange={this.cvvChange} type="text" />
                                    <h5>ZipCode</h5>
                                    <input className="form-control" value={this.state.zipcode} onChange={this.zipcodeChange} type="text" />
                                </div>

                                </div>
                            </div>
                        </div>
                        </div>
                        <div className="card " style={{"width": "18rem","margin": "0 auto"}}>
                        <div className="card-body">
                            <div className="row">
                                <div className="col-md-12">
                                    <h6>Amount</h6>
                                    <h5><input className="form-control" value={this.state.amount} onChange={this.amountChange} type="number" /></h5>
                                    
                                </div>
                                <button className="btn btn-primary" style={{"marginTop": "20px"}} onClick={this.submit}>Confirm Payment</button>
                            </div>
                        </div>
                        </div>
                        
                        

                        
                    </div>
                </div>
         </div>
        )
    }
    componentDidMount(){
        this.usernameChange(localStorage.getItem("Username"))

    }
}

export default Newwallet;


