import React from "react";
import { Textbox, Textarea, Radiobox, Checkbox, Select } from 'react-inputs-validation';
import 'react-inputs-validation/lib/react-inputs-validation.min.css';
import UserAvatar from "react-user-avatar";
import { extname } from "path";
import "../exam.css";
import {createFragmentContainer, commitMutation,graphql} from "react-relay";
import environment from "../../environment";

const SettingsMutation = graphql`
    mutation SettingsMutation($username: String, $fname: String, $lname: String, $email: String){
                updateSettings(username: $username, fname: $fname, lname: $lname, email: $email){
                    username
                }
        
    }
`

 class Settings extends React.Component{

    constructor() {
        super();
        this.state = {
          fname: '',
          lname: '',
          username: '',
          email: ''
        }
        this.onfnameChange = this.onfnameChange.bind(this);
        this.onlnameChange = this.onlnameChange.bind(this);
        this.onemailChange = this.onemailChange.bind(this);
        this.usernameChange = this.onusernameChange.bind(this);
        this.submit = this.submit.bind(this);
       
      }
    onfnameChange(event){
      if(event.target != null){
        this.setState({fname: event.target.value});
      }
      else{
        this.setState({fname: event});
      }
        
    }
    onlnameChange(event){
      if(event.target != null){
        this.setState({lname: event.target.value});
      }
      else{
        this.setState({lname: event});
      }
    }
    onemailChange(event){
      if(event.target != null){
        this.setState({email: event.target.value});
      }
      else{
        this.setState({email: event});
      }
    }
    onusernameChange(event){
      this.setState({username: event});
  }
  submit(){
    const variables = {
      email: this.state.email,
      fname: this.state.fname,
      lname: this.state.lname,
      username: this.state.username
    }
    commitMutation(environment,{
      mutation: SettingsMutation,
      variables,
      onCompleted: (response) => {
          const user = response.updateSettings;
          if(user !== null){
                window.location.reload()
              
          }
      }
  })
    
  }
      
  

    render(){
        return (
            <div className="container-fluid" style={{marginTop: "10px"}}>
                <div className="row" style={{height: "250px"}}>
                    <div  style={{margin: "0 auto"}}>
                        <div style={{textAlign: "center"}}>

                            
                            
                             <UserAvatar size="100" name={localStorage.getItem('Username')} colors={['whitesmoke']} className="UserAvatar--inner"/>
                             <h6 style={{color: "#00c730"}}>{localStorage.getItem('Username')}</h6>
                                            

                        </div>
                    </div>  
                </div>
                <hr />
                <div className="form-update">
                <div className="text-center mb-4">
                <h1 className = "h3 mb-3 font-weight-normal">Update your Information</h1>
                
                </div>


                <div className="form-label-group">
                        <input type="name" id="inputfname" className="form-control" placeholder="First Name" required autofocus value={this.state.fname} onChange={this.onfnameChange} />
                        <label for="inputfname">First Name</label>
                    </div>
                    <div className="form-label-group">
                            <input type="name" id="inputlname" className="form-control" placeholder="Last Name" required autofocus value={this.state.lname} onChange={this.onlnameChange} />
                            <label for="inputlname">Last Name</label>
                    </div>
                    
                <div className="form-label-group">
                    <input type="email" id="inputEmail" className="form-control" placeholder="Email address" required autofocus  value={this.state.email} onChange={this.onemailChange}/>
                    <label for="inputEmail">Email address</label>
                </div>
                <button className="btn btn-lg btn-success btn-block"  onClick={this.submit}>Update!</button>
                <p className="mt-5 mb-3 text-muted text-center"> 2019 SecurePay</p>
                </div>
                

                </div>
                
        )
    }

    componentDidMount(){
      this.onusernameChange(localStorage.getItem("Username"))
      this.onfnameChange(this.props.settings.fname);
      this.onlnameChange(this.props.settings.lname);
      this.onemailChange(this.props.settings.email);
    }
    

}



export default createFragmentContainer(Settings, graphql`
    fragment Settings_settings on User{
        fname
        lname
        email
    }
`)